<template>
  <div class="todo-wrapper">
    <!-- tab选项 -->
    <tab :filter="filter" :todos="todos" @toggleState="toggleToDos" />
    <!-- todo列表 -->
    <ul class="todo-list">
      <li is="item" v-for="todo in filteredTodos" :key="todo.id" :todo="todo" @toDel="delToDo" @toUpdate="updateToDo"></li>
    </ul>
    <!-- 底部tab -->
    <bottom-tab @clear="clearCompleted" :num="activeNum" />
    <!-- 输入框 -->
    <input type="text" :placeholder="title" @keydown.enter="addToDo" />
  </div>
</template>

<script>
  import store from '../base/store'
  import item from './item'
  import tab from './tab'
  import bottomTab from './bottomTab'
  let todos = store.get(),
    id = todos[0] ? todos[0].id + 1 : 0;
  export default {
    components: {
      item,
      tab,
      bottomTab
    },
    data() {
      return {
        title: '接下来要做什么？',
        todos: todos,
        filter: 'All'
      }
    },
    computed: {
      filteredTodos() {
        if (this.filter === 'All') {
          return this.todos;
        }
        const completed = this.filter === 'Completed';
        return this.todos.filter(todo => todo.isCompleted === completed);
      },
      activeNum() {
        return this.todos.filter(todo => !todo.isCompleted).length;
      }
    },
    methods: {
      addToDo(e) {
        if (e.target.value === '') return;
        this.todos.unshift({
          id: id++,
          content: e.target.value.trim(),
          isCompleted: false
        });
        e.target.value = '';
        this.$notify({
          content: '你又多一件事待做！'
        })
      },
      delToDo(key) {
        this.todos.splice(this.todos.findIndex(todo => todo.id === key), 1);
        this.$notify({
          content: '你又少一件事待做了...'
        })
      },
      updateToDo(item) {
        this.todos.map(todo => todo.id === item.id ? item : todo);
        this.$notify({
          content: '厉害厉害，做完了一件事！'
        })
      },
      toggleToDos(state) {
        this.filter = state;
      },
      clearCompleted() {
        if(this.todos.find(todo => todo.isCompleted === true)){
          this.$notify({
            content: '厉害炸了，这么多完成的事被清除！'
          })
        }else{
            this.$notify({
              content: '没一件事被完成了，你清除个啥？'
            })
        }
        this.todos = this.todos.filter(todo => !todo.isCompleted);
      }
    },
    watch: {
      todos: {
        handler: function() {
          store.save(this.todos);
        },
        deep: true
      }
    },
    mounted() {
      let documentHeight = $(document).height();
      if (documentHeight > 570) {
        $(".todo-list").css('height', documentHeight * .49)
      } else {
        $(".todo-list").css('height', documentHeight * .38)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .todo-wrapper {
    width: 90%;
    ul {
      list-style: none;
      overflow: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background-color: rgba($color:#4199ee, $alpha: .5);
      }
      li {
        margin: .7em auto;
        width: 95%;
        border-radius: 7px;
        box-shadow: 1px 0 10px rgba($color: #cdcdcd, $alpha: .6);
        transition: all .5s ease;
      }
    }
    input {
      position: absolute;
      bottom: .6em;
      font-size: 1em;
      height: 3em;
      width: 90%;
      outline: none;
      border: 0;
      padding: 0 1em;
      margin-bottom: 1em;
      box-sizing: border-box;
      border-bottom: .8px solid rgba($color:#000, $alpha: .4);
      &:focus {
        border-bottom: 2px solid rgba($color:#4199ee, $alpha: 1);
      }
    }
  }
</style>



