<template>
  <div class="tab-wrapper bottom-tab">
    <span>{{num}} Items ToDo</span>
    <div class="clear"
      @click="clear"
    >Clear Completed</div>
  </div>
</template>

<script>
  export default {
    props: {
      num: Number,
      todos: Array
    },
    methods: {
      clear() {
        this.$emit('clear');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .bottom-tab{
    font-size: .9em;
    font-weight: 550;
    .clear {
      cursor: pointer;
      user-select: none;
    }
  }
</style>


