import Notification from './notification.vue'

export default {
  extends: Notification,
  data() {
    return {
      verticalOffsetHeight: 0,
      autoTime: 2500,
      height: 0
    }
  },
  computed: {
    style() {
      return {
        position: 'fixed',
        right: '2em',
        top: `${this.verticalOffsetHeight}px`
      }
    }
  },
  mounted() {
    this.createTimeout();
  },
  beforeDestroy() {
    this.clearTimeout();
  },
  methods: {
    createTimeout() {
      if (this.autoTime) {
        this.timer = setTimeout(() => {
          this.visible = false;
        }, this.autoTime);
      }
    },
    clearTimeout() {
      if (this.timer) {
        clearTimeout(this.timer); 
      }
    },
    afterEnter() {
       this.height = this.$el.offsetHeight;
    }
  }
}


