<template>
  <transition name="notify-in-out" 
    @after-enter="afterEnter"
    @after-leave="afterLeave"
  >
    <div class="notification"
      :style="style"
      v-show="visible"
      @mouseover="clearTimeout"
      @mouseout="createTimeout"
    >
      <span class="content">{{content}}</span>
      <a class="btn"
        @click.prevent="notificationClose"
      >{{btn}}</a>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Notification',
  props: {
    content: {
      type: String,
      required: true
    },
    btn: {
      type: String,
      default: 'Close'
    }
  },
  data() {
    return {
      visible: false
    }
  },
  mounted() {
    this.visible = true
  },
  computed: {
    style() {
      return {}
    }
  },
  methods: {
    notificationClose() {
      this.$emit('notifyClose');
    },
    afterEnter() {
    },
    afterLeave() {
      this.$emit('notifyClosed');
    },
    createTimeout() {},
    clearTimeout() {}
  }
}
</script>

<style lang="scss" scoped>

.notify-in-out-enter, .notify-in-out-leave-to{
  transform: translateY(-2em);
  opacity: 0;
}
.notify-in-out-enter-active,.notify-in-out-leave-active{
  transition: all .4s;
}

.notification{
  max-width: 14em;
  background-color: #232323;
  padding: 1em 4em 1em 1.4em;
  border-radius: 5px;
  box-shadow: 0 10px 20px rgba($color: #000, $alpha: .4);
  position: fixed;
  font-style: italic;
  font-size: .9em;
  font-weight: 500;
  transition: all .2s;
  .content{
    color: snow;
  }
  .btn{
    position: absolute;
    color: #4199ee;
    right: .6em;
    top: .5em;
    cursor: pointer;
  }
}
</style>



