<template>
<transition name="item-in-out">
  <li :class="{completed : todo.isCompleted}">
    <input type="checkbox" class="check-btn" 
      v-model="todo.isCompleted" 
      @click="toUpdate"
    />
    <label>{{todo.content}}</label>
    <div class="del-btn"
      @click="toDel"
    ></div>
  </li>
</transition>
</template>

<script>
  export default {
    props: {
      todo: {
        id: Number,
        content: String,
        isCompleted: Boolean
      }
    },
    methods: {
      toDel(){
        this.$emit('toDel',this.todo.id);
      },
      toUpdate(){
        this.$emit('toUpdate',this.todo);
      }
    }
  }
</script>

<style lang="scss" scoped>
// 动画
.item-in-out-enter,.item-in-out-leave-to{
  transform: translateY(-5em);
  opacity: 0.4;
}
.item-in-out-enter-active,.item-in-out-leave-active{
  transition: all .4s ease;
}

  li {
    position: relative;
    display: block;
    box-sizing: border-box;
    padding: 1.2em 1em 1.2em 0.4em;
    .check-btn {
      -webkit-appearance: none;
      outline: none;
      border: 0;
      display: block;
      border: 1px solid #e8e6e6;
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: .8em;
      transform: translateY(-50%);
      transition: all .5s ease-in-out;
    }
    label {
      display: block;
      box-sizing: border-box;
      padding-left: 2.1em;
      padding-right: 1em;
      font-size: 1em;
      font-weight: 500;
      line-height: 1em;
      word-wrap: break-word;
    }
    .del-btn{
      position: absolute;
      top: 50%;
      right: .2em;
      transform: rotate(0) translateY(-50%);
      width: 1.2em;
      height: 1.2em;
      outline: 9.4px solid #4199ee;
      outline-offset: -17px;
      opacity: 0;
      transition: all .4s ease;
    }
    &:hover{
      .del-btn{
        opacity: 1;
        transform: rotate(-45deg) translateY(-50%);
      }
    }
  }
  .completed{
    
    .check-btn{
      background-color: #4199ee;
       &:before {
        content: '';
        display: block;
        border-left: .2em solid snow;
        border-bottom: .2em solid snow;
        border-radius: .2em;
        width: .7em;
        height: .4em;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: rotate(-45deg) translate(-5%, -105%);
      }
    }
    label{
      color: #969da5;
      text-decoration: line-through;
      text-decoration-color: black;
    }
  }
</style>



