<template>
	<div class="header-wrapper">
    <h1>To-Do</h1>
  </div>
</template>

<script>
export default {
  data(){
    return {
      text: 'hello'
    }
  }
}
</script>

<style lang="scss" scoped>
.header-wrapper{
  width: 100%;
  padding: 4em 0 1em 1.5em;
  background-color: #4199ee;
  box-sizing: border-box;
  box-shadow: 0 6px 20px rgba($color: #cdcdcd, $alpha: .6);
  h1{
    color: seashell;
  }
}
</style>




