import Vue from 'vue'
import Component from './funcNotification'

const notificationConstructor = Vue.extend(Component);

let instances = [],
    seed = 1;

const _removeInstance = (instance) => {
  if (!instance) return;
  const len = instances.length;
  const index = instances.findIndex(ins => ins.id === instance.id);
  instances.splice(index, 1);

  if (len > 1) {    
    const reomeHeight = instance.vm.height;
    for (let i = index; i < len -1; i++) {    
      instances[i].verticalOffsetHeight = parseInt(instances[i].verticalOffsetHeight) - reomeHeight - 10;
    }
  }

}

const notify = (option) => {

  let {
    autoTime,
    ...reset
  } = option;
  let instance = new notificationConstructor({
    data: {
      autoTime: autoTime === undefined ? 3000 : autoTime
    },
    propsData: { ...reset }
  });
  
  let id = `notification_${seed++}`;
  instance.id = id;  

  instance.vm = instance.$mount();
  document.body.appendChild(instance.vm.$el);

  let verticalOffsetHeight = 0;
  instances.forEach(item => {
    verticalOffsetHeight += item.$el.offsetHeight + 10;
  });
  verticalOffsetHeight += 10;
  instance.verticalOffsetHeight = verticalOffsetHeight;
  instances.push(instance);


    instance.vm.$on('notifyClosed', () => {
      _removeInstance(instance);
      document.body.removeChild(instance.vm.$el);
      instance.vm.$destroy();
    });
    instance.vm.$on('notifyClose',() => {
      instance.vm.visible = false;
    });

    return instance.vm;
}

export default notify


