import Vue from 'vue'
import App from './App'
// import router from './router'
import Notification from './components/notification'

Vue.config.productionTip = false
Vue.config.devtools = false

Vue.use(Notification)

new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



